import React, { useState, useEffect } from 'react';
import axios from "axios";
import Cookies from "universal-cookie";
const cookies = new Cookies();

export default function Login(){
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [login, setLogin] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();

        const configuration = {
            method: "post",
            url: "http://localhost:8080/account/login",
            withCredentials: true,
            data: {
                email,
                password,
            },
        };

        // make the API call
        axios(configuration)
            .then((result) => {
                // set the cookie
                cookies.set("TOKEN", result.data.token, {
                    path: "/",
                });
                // redirect user to the auth page
                //window.location.href = "/auth";

                setLogin(true);
            })
            .catch((error) => {
                error = new Error();
            });
    }


    return(
        <div>
            <form onSubmit={(e) => handleSubmit(e)}>
                <input value={email}     onChange={(e) => setEmail(e.target.value)}     name="email"     placeholder={`email`}   />
                <input value={password}  onChange={(e) => setPassword(e.target.value)}  name='password'  placeholder={`password`}/>
                <button type={"submit"}>Login</button>
            </form>
            {login ? (
                <p className="text-success">You Are Logged in!!!</p>
            ) : (
                <p className="text-danger">You are not Logged in!</p>
            )}
        </div>
    )

};